@-webkit-keyframes slideInFromTop {
  0% {
    -webkit-transform: translateY(-200%);
            transform: translateY(-200%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slideInFromTop {
  0% {
    -webkit-transform: translateY(-200%);
            transform: translateY(-200%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.Board {
  width: 400px;
  border: 1px solid #DDD;
  border-radius: 3px;
  background: #FFF;
  margin: auto;
}

.BoardInput {
  background: #F4F6F6;
}

.BoardInput .Text {
  display: -ms-flexbox;
  display: flex;
}
.BoardInput .Text input {
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: 120%;
}
.BoardInput .Actions {
   text-align: right;
   padding: 5px;
}
.BoardInput .Actions .SendBtn {
  border-radius: 15px;
  border: 0;
  background: #3498DB;
  color: #FFF;
  padding: 5px 10px;
  margin: 5px;
}
.BoardInput .Actions .SendBtn:hover {
  -webkit-box-shadow: 3px 3px 10px #CCC;
          box-shadow: 3px 3px 10px #CCC;
}

.MessageList {
  padding-top: 1em;
  padding: 10px;
  min-height: 300px;
  background: #EAEDED;
  overflow: hidden;
}
.Message {
  background: #fff;
  border-radius: 5px;
  border: 1px solid #BBB;
  padding: 0 5px 5px 5px;
  margin-bottom: 1em;
}
.Message.Animated {
  -webkit-animation: slideInFromTop 1s ease-out 0s 1 normal both;
          animation: slideInFromTop 1s ease-out 0s 1 normal both;
}
.Message .Heading{
  text-align: right;
  height: 1.5em;
}
.Message .Heading .Delete{
  font-size: 50%;
  cursor: pointer;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.Message .Heading .Delete:hover{
  font-size: 100%;

}
.Message .Body{
}


.message-enter {
  opacity: 0.01;
}

.message-enter.message-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.message-leave {
  opacity: 1;
}

.message-leave.message-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 400ms ease-in;
  -o-transition: opacity 400ms ease-in;
  transition: opacity 400ms ease-in;
}



